import { Camera } from './Camera'
import texturedVertex from './shaders/textured.vert.glsl'
import texturedFrag from './shaders/textured.fxaa.frag.glsl'
import basicVertex from './shaders/basic.vert.glsl'
import coloredFrag from './shaders/colored.frag.glsl'
import * as Utils from './Utils'
import * as Olic from './olic'

export class OlicCanvas {
  constructor(canvas, fieldOptions, options, colorOptions, size) {
    this.size = size
    this.pxRatio = Math.max(Math.floor(window.devicePixelRatio) || 1, 2)
    //canvas.style.width = this.size + 'px'
    //canvas.style.height = this.size + 'px'
    //canvas.width = canvas.clientWidth * this.pxRatio
    //canvas.height = canvas.clientHeight * this.pxRatio
    canvas.width = size
    canvas.height = size
    this.startSize = size
    this.fieldOptions = fieldOptions
    this.colorOptions = colorOptions
    this.options = options
    this.gl = canvas.getContext('webgl', { antialiasing: false })
    this.camera = new Camera(
      this.gl,
      canvas.width,
      canvas.height,
      this.startSize,
    )

    //this.gl.viewport(0, 0, this.startSize * 4, this.startSize * 4)
    this.canvas = canvas
    this.render = this.render.bind(this)

    this.texturedProgramWrapper = Utils.initShaderProgram(
      this.gl,
      texturedVertex,
      texturedFrag,
    )
    this.coloredProgramWrapper = Utils.initShaderProgram(
      this.gl,
      basicVertex,
      coloredFrag,
    )

    this.texturedBuffers = this.createBuffers(this.gl)
    this.backgroundBuffers = this.createColoredBuffers(this.gl)
    this.offset = { x: 0, y: 0 }
    this.count = 0
    this.noiseNeedsUpdate = false
    this.textures = this.createTextures()
  }

  createTextures() {
    let texData = null
    let textures = []
    let pixelSize = this.options.size
    //let pixelSize = 2
    let newWidth = this.canvas.width / pixelSize
    let newHeight = this.canvas.height / pixelSize
    let precalculateResult = Olic.precalculateLic(
      newWidth,
      newHeight,
      this.options.lineLength,
      this.fieldOptions,
      this.options,
      this.colorOptions,
      this.options.speed,
      this.options.texture,
    )
    let sourceDroplets = precalculateResult.sourceDroplets
    let sourceOffsets = precalculateResult.sourceOffsets
    let noiseTexture = precalculateResult.texture
    let maxValue = precalculateResult.maxValue
    for (let t = 0; t < this.options.lineLength; t++) {
      texData = Olic.drawLicToImage(
        this.canvas.width,
        this.canvas.height,
        this.options.lineLength,
        t,
        this.fieldOptions,
        this.options,
        this.colorOptions,
        pixelSize,
        this.options.scale,
        sourceOffsets,
        sourceDroplets,
        noiseTexture,
        maxValue,
      )
      textures.push(
        Utils.createTexture(
          this.gl,
          this.gl.NEAREST,
          texData,
          newWidth,
          newHeight,
        ),
      )
    }
    //debugger
    return textures
    //this.textures.push()
  }

  updateTextures() {
    Olic.resetSourceOffsets()
    this.textures = null
    let newTextures = this.createTextures()
    this.textures = newTextures
  }

  init() {
    if (this.gl === null) {
      alert(
        'Unable to initialize WebGL. Your browser or machine may not support it.',
      )
      return
    }

    // Set clear color to black, fully opaque
    this.gl.clearColor(0.0, 0.0, 0.0, 1.0)
    // Clear the color buffer with specified clear color
    this.gl.clear(this.gl.COLOR_BUFFER_BIT)
  }

  render(t) {
    //debugger
    this.draw(
      this.camera.getProjectionMatrix(),
      this.camera.getModelViewMatrix(),
      this.gl,
      t,
    )
  }

  updateSize(size) {
    //debugger
    this.size = size
    this.canvas.width = size
    this.canvas.height = size
    this.canvas.style.width = size + 'px'
    this.canvas.style.height = size + 'px'

    // this.canvas.style.width = this.size + 'px'
    // this.canvas.style.height = this.size + 'px'
    // this.canvas.width = this.canvas.clientWidth * this.pxRatio
    // this.canvas.height = this.canvas.clientHeight * this.pxRatio
    this.camera.update(
      this.gl,
      this.canvas.width,
      this.canvas.height,
      this.size,
    )
    this.updateTextures()
  }

  createBuffers(gl) {
    let positions = new Float32Array([0, 0, 1, 0, 0, 1, 1, 1])
    //let textureCoords = new Float32Array([0, 0, 1, 0, 0, 1, 1, 1])
    let textureCoords = new Float32Array([0, 1, 1, 1, 0, 0, 1, 0])
    let indexes = new Uint16Array([0, 2, 1, 1, 2, 3])

    let positionBuffer = Utils.createBuffer(gl, positions)
    let textureBuffer = Utils.createBuffer(gl, textureCoords)
    let indexBuffer = Utils.createIndexBuffer(gl, indexes)
    return {
      positions: positionBuffer,
      textureCoords: textureBuffer,
      indexes: indexBuffer,
    }
  }

  createColoredBuffers(gl) {
    let positions = new Float32Array([0, 0, 1, 0, 0, 1, 1, 1])
    let indexes = new Uint16Array([0, 2, 1, 1, 2, 3])

    let positionBuffer = Utils.createBuffer(gl, positions)
    let indexBuffer = Utils.createIndexBuffer(gl, indexes)
    return {
      positions: positionBuffer,
      indexes: indexBuffer,
    }
  }

  draw(projectionMatrix, modelViewMatrix, gl, t) {
    //debugger
    let textureIndex = Math.floor(
      (t * this.options.speed) % this.options.lineLength,
    )
    // if (this.noiseNeedsUpdate) {
    //   this.noiseTexture = Utils.makeNoiseTexture(
    //     this.gl,
    //     this.options.size,
    //     this.colorOptions,
    //     this.options.alpha,
    //     this.options.proportion,
    //   )
    //   this.noiseNeedsUpdate = false
    // }

    Utils.bindAttribute(
      this.gl,
      this.texturedBuffers.positions,
      this.texturedProgramWrapper.aVertexPosition,
      2,
    )
    Utils.bindAttribute(
      this.gl,
      this.texturedBuffers.textureCoords,
      this.texturedProgramWrapper.aTextureCoord,
      2,
    )
    this.gl.useProgram(this.texturedProgramWrapper.program)

    this.gl.uniformMatrix4fv(
      this.texturedProgramWrapper.uProjectionMatrix,
      false,
      projectionMatrix,
    )
    this.gl.uniformMatrix4fv(
      this.texturedProgramWrapper.uModelViewMatrix,
      false,
      modelViewMatrix,
    )
    this.gl.uniform2f(
      this.texturedProgramWrapper.resolution,
      this.canvas.width,
      this.canvas.height,
    )
    Utils.bindTexture(gl, this.textures[textureIndex], 0)
    gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.texturedBuffers.indexes)
    gl.drawElements(gl.TRIANGLES, 6, gl.UNSIGNED_SHORT, 0)

    gl.disable(gl.BLEND)
  }

  updateAngleAndColor(fieldOptions, colorOptions) {
    this.fieldOptions = fieldOptions
    this.colorOptions = colorOptions
    this.updateTextures()
  }

  updateNoiseTexture() {
    this.noiseNeedsUpdate = true
  }

  // getOptions() {
  //   return this.options
  // }
}
