import * as Utils from './Utils'

import texturedVertex from './shaders/textured.vert.glsl'
import texturedFragment from './shaders/textured.frag.glsl'
import texturedSpeedVertex from './shaders/textured_linearspeed.vert.glsl'
import coloredFragment from './shaders/colored.frag.glsl'

export class ParticlesCanvas {
  constructor(
    gl,
    nMesh,
    distanceMesh,
    width,
    height,
    pixelsPerUnit,
    size,
    fieldOptions,
    colorOptions,
    startSize,
    noiseOptions,
  ) {
    this.gl = gl
    this.center = { x: 0, y: 0 }
    this.realWidth = 1
    this.realHeight = 1
    this.texturedSpeedProgramWrapper = Utils.initShaderProgram(
      this.gl,
      texturedSpeedVertex,
      texturedFragment,
    )
    this.texturedProgramWrapper = Utils.initShaderProgram(
      this.gl,
      texturedVertex,
      texturedFragment,
    )
    this.buffers = this.createBuffers(gl, nMesh, distanceMesh)
    this.textureQuadBuffers = this.createTextureQuad(gl)
    this.screenQuadBuffers = this.createScreenQuad(
      gl,
      width,
      height,
      pixelsPerUnit,
    )

    this.draw = this.draw.bind(this)
    this.nPoints = nMesh * (nMesh + 1) * 2
    this.nMesh = nMesh
    this.scale = 1
    this.npix = size
    this.dmax = this.scale / this.npix
    this.npat = 32
    this.patResolution = 32

    this.fieldOptions = fieldOptions
    noiseOptions == undefined
      ? (this.noiseOptions = {
          npix: size,
          scale: 2,
          nPat: 32,
          patResolution: 128,
          alpha: 20,
          proportion: 0.5,
          distThreshold: 0,
          speed: 50,
          framesPerSecond: 60,
        })
      : (this.noiseOptions = noiseOptions)
    this.noiseOptions.size = size
    this.patternsNeedUpdate = false
    this.lastAlpha = 20
    this.colorOptions = colorOptions
    this.iframe = 0
    this.patterns = Utils.makePatterns(
      this.gl,
      this.noiseOptions.nPat,
      startSize,
      this.noiseOptions.alpha,
      this.noiseOptions.proportion,
      this.colorOptions.background,
      this.colorOptions.particles,
      this.noiseOptions.scale,
    )
    //debugger
    this.createTextures(this.gl, width, height)
    this.framebuffer = gl.createFramebuffer()
    this.accumulatedDistance = this.noiseOptions.distThreshold
    this.lastT = 0
    this.size = startSize
  }

  createBuffers(gl, nMesh, distanceMesh) {
    const faces = (nMesh + 1) * (nMesh + 1)
    // let positions = new Float32Array(faces * 2)
    // for (let i = 0; i < nMesh; i++) {
    //   for (let j = 0; j < nMesh; j++) {
    //     let index = i * (nMesh + 1) * 2 + j * 2
    //     positions[index * 2] = (j / nMesh) * distanceMesh
    //     positions[index * 2 + 1] = (i / nMesh) * distanceMesh
    //     index++
    //     positions[index * 2] = (j / nMesh) * distanceMesh
    //     positions[index * 2 + 1] = ((i + 1) / nMesh) * distanceMesh
    //   }
    // }

    let positions = new Float32Array(faces * 2)
    let textureCoords = new Float32Array(faces * 2)
    for (let i = 0; i < nMesh + 1; i++) {
      for (let j = 0; j < nMesh + 1; j++) {
        positions[i * (nMesh + 1) * 2 + j * 2] = j / nMesh * distanceMesh
        positions[i * (nMesh + 1) * 2 + j * 2 + 1] = i / nMesh * distanceMesh
        textureCoords[i * (nMesh + 1) * 2 + j * 2] = j / nMesh
        textureCoords[i * (nMesh + 1) * 2 + j * 2 + 1] = i / nMesh
      }
    }
    //console.log('positions', positions)
    let indexes = new Uint16Array(nMesh * nMesh * 2 * 3)
    for (let i = 0; i < nMesh; i++) {
      for (let j = 0; j < nMesh; j++) {
        indexes[i * nMesh * 6 + j * 6] = i * (nMesh + 1) + j
        indexes[i * nMesh * 6 + j * 6 + 1] = (i + 1) * (nMesh + 1) + j
        indexes[i * nMesh * 6 + j * 6 + 2] = i * (nMesh + 1) + j + 1

        indexes[i * nMesh * 6 + j * 6 + 3] = (i + 1) * (nMesh + 1) + j
        indexes[i * nMesh * 6 + j * 6 + 4] = (i + 1) * (nMesh + 1) + (j + 1)
        indexes[i * nMesh * 6 + j * 6 + 5] = i * (nMesh + 1) + j + 1
      }
    }
    //debugger
    let positionBuffer = Utils.createBuffer(gl, positions)
    let textureBuffer = Utils.createBuffer(gl, textureCoords)
    let indexBuffer = Utils.createIndexBuffer(gl, indexes)
    return {
      positions: positionBuffer,
      textureCoords: textureBuffer,
      indexes: indexBuffer,
    }
  }

  createTextureQuad(gl) {
    let positions = new Float32Array([0, 0, 1, 0, 0, 1, 1, 1])
    let textureCoords = new Float32Array([0, 0, 1, 0, 0, 1, 1, 1])
    let indexes = new Uint16Array([0, 2, 1, 1, 2, 3])

    let positionBuffer = Utils.createBuffer(gl, positions)
    let textureBuffer = Utils.createBuffer(gl, textureCoords)
    let indexBuffer = Utils.createIndexBuffer(gl, indexes)
    return {
      positions: positionBuffer,
      textureCoords: textureBuffer,
      indexes: indexBuffer,
    }
  }

  createScreenQuad(gl, width, height, pixelsPerUnit) {
    let positions = new Float32Array([0, 0, 1, 0, 0, 1, 1, 1])

    let textureCoords = new Float32Array([0, 1, 1, 1, 0, 0, 1, 0])
    let indexes = new Uint16Array([0, 2, 1, 1, 2, 3])

    let positionBuffer = Utils.createBuffer(gl, positions)
    let textureBuffer = Utils.createBuffer(gl, textureCoords)
    let indexBuffer = Utils.createIndexBuffer(gl, indexes)
    return {
      positions: positionBuffer,
      textureCoords: textureBuffer,
      indexes: indexBuffer,
    }
  }

  createTextures(gl, width, height) {
    const emptyPixels = new Uint8Array(width * height * 4)
    this.backgroundTexture = Utils.createTexture(
      gl,
      gl.NEAREST,
      emptyPixels,
      width,
      height,
      gl.RGB,
    )
    this.screenTexture = Utils.createTexture(
      gl,
      gl.NEAREST,
      emptyPixels,
      width,
      height,
      gl.RGB,
    )
  }

  // getDP(x, y, sa) {
  //   let dx = x - 0.5
  //   let dy = y - 0.5
  //   let r = dx * dx + dy * dy
  //   if (r < 0.0001) {
  //     r = 0.0001
  //   }
  //   let vx = sa * dx / r + 0.02
  //   let vy = sa * dy / r
  //   r = vx * vx + vy * vy
  //   if (r > this.dmax * this.dmax) {
  //     r = Math.sqrt(r)
  //     vx *= this.dmax / r
  //     vy *= this.dmax / r
  //   }
  //   return { vx: vx, vy: vy }
  // }

  draw(projectionMatrix, modelViewMatrix, gl, camera, t) {
    if (this.patternsNeedUpdate) {
      this.patterns = Utils.makePatterns(
        this.gl,
        this.noiseOptions.nPat,
        this.size,
        this.noiseOptions.alpha,
        this.noiseOptions.proportion,
        this.colorOptions.background,
        this.colorOptions.particles,
        this.noiseOptions.scale,
      )
      //debugger
      this.accumulatedDistance = this.distThreshold
      this.patternsNeedUpdate = false
    }
    this.drawFBO(projectionMatrix, modelViewMatrix, gl, camera, t)
    this.drawScreen(projectionMatrix, modelViewMatrix, gl)

    let temp = this.backgroundTexture
    this.backgroundTexture = this.screenTexture
    this.screenTexture = temp
  }

  drawFBO(projectionMatrix, modelViewMatrix, gl, camera, t) {
    Utils.bindFramebuffer(gl, this.framebuffer, this.screenTexture)
    {
      let sa = 0.01 * Math.cos(this.iframe * 2.0 * Math.PI / 200)
      //console.log('sa ' + sa)
      Utils.bindAttribute(
        this.gl,
        this.buffers.positions,
        this.texturedSpeedProgramWrapper.aVertexPosition,
        2,
      )
      Utils.bindAttribute(
        this.gl,
        this.buffers.textureCoords,
        this.texturedSpeedProgramWrapper.aTextureCoord,
        2,
      )
      this.gl.useProgram(this.texturedSpeedProgramWrapper.program)
      this.gl.uniformMatrix4fv(
        this.texturedSpeedProgramWrapper.uProjectionMatrix,
        false,
        camera.projectionMatrix,
      )
      this.gl.uniformMatrix4fv(
        this.texturedSpeedProgramWrapper.uModelViewMatrix,
        false,
        camera.modelViewMatrix,
      )
      // this.gl.uniform1f(this.texturedSpeedProgramWrapper.dmax, this.dmax)
      // this.gl.uniform1f(this.texturedSpeedProgramWrapper.sa, sa)
      let angle = this.fieldOptions.angle * Math.PI / 180
      // let offsetX =
      //   (t - this.lastT) *
      //   this.noiseOptions.speed *
      //   Math.cos(angle) /
      //   camera.getPixelsPerUnit()
      // let offsetY =
      //   (t - this.lastT) *
      //   this.noiseOptions.speed *
      //   Math.sin(angle) /
      //   camera.getPixelsPerUnit()

      let offsetX =
        (t - this.lastT) *
        this.noiseOptions.speed *
        Math.cos(angle) /
        camera.getPixelsPerUnit()
      let offsetY =
        (t - this.lastT) *
        this.noiseOptions.speed *
        Math.sin(angle) /
        camera.getPixelsPerUnit()
      //console.log('camera.getPixelsPerUnit()', camera.getPixelsPerUnit())
      //console.log('offsetX', offsetX)
      //console.log('offsetY', offsetY)
      // this.gl.uniform2f(
      //   this.texturedSpeedProgramWrapper.offset,
      //   this.fieldOptions.vx / camera.getPixelsPerUnit(),
      //   this.fieldOptions.vy / camera.getPixelsPerUnit(),
      // )
      this.gl.uniform2f(
        this.texturedSpeedProgramWrapper.offset,
        offsetX,
        offsetY,
      )
    }
    gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.buffers.indexes)
    Utils.bindTexture(gl, this.backgroundTexture, 0)
    //Utls.bindTexture(gl, this.patterns[0])
    gl.drawElements(
      gl.TRIANGLES,
      this.nMesh * this.nMesh * 2 * 3,
      gl.UNSIGNED_SHORT,
      0,
    )

    //drawRandomTexture
    // if (this.accumulatedDistance >= this.noiseOptions.distThreshold) {
    this.accumulatedDistance = 0
    this.iframe++
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    Utils.bindAttribute(
      this.gl,
      this.textureQuadBuffers.positions,
      this.texturedProgramWrapper.aVertexPosition,
      2,
    )
    Utils.bindAttribute(
      this.gl,
      this.textureQuadBuffers.textureCoords,
      this.texturedProgramWrapper.aTextureCoord,
      2,
    )
    this.gl.useProgram(this.texturedProgramWrapper.program)
    this.gl.uniformMatrix4fv(
      this.texturedProgramWrapper.uProjectionMatrix,
      false,
      projectionMatrix,
    )
    this.gl.uniformMatrix4fv(
      this.texturedProgramWrapper.uModelViewMatrix,
      false,
      modelViewMatrix,
    )
    Utils.bindTexture(gl, this.patterns[this.iframe % this.npat], 0)
    gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.textureQuadBuffers.indexes)
    gl.drawElements(gl.TRIANGLES, 6, gl.UNSIGNED_SHORT, 0)
    gl.disable(gl.BLEND)
    // } else {
    //   this.accumulatedDistance += (t - this.lastT) * this.fieldOptions.vx
    // }
    Utils.bindFramebuffer(gl, null)
    this.lastT = t
    //gl.copyTexImage2D(gl.TEXTURE_2D, 0, gl.RGB, 0, 0, this.npix, this.npix, 0)
    //this.gl.drawArrays(this.gl.TRIANGLE_STRIP, 0, this.nPoints)
  }

  drawScreen(projectionMatrix, modelViewMatrix, gl) {
    Utils.bindAttribute(
      this.gl,
      this.screenQuadBuffers.positions,
      this.texturedProgramWrapper.aVertexPosition,
      2,
    )
    Utils.bindAttribute(
      this.gl,
      this.screenQuadBuffers.textureCoords,
      this.texturedProgramWrapper.aTextureCoord,
      2,
    )
    this.gl.useProgram(this.texturedProgramWrapper.program)
    this.gl.uniformMatrix4fv(
      this.texturedProgramWrapper.uProjectionMatrix,
      false,
      projectionMatrix,
    )
    this.gl.uniformMatrix4fv(
      this.texturedProgramWrapper.uModelViewMatrix,
      false,
      modelViewMatrix,
    )
    Utils.bindTexture(gl, this.screenTexture, 0)
    gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.screenQuadBuffers.indexes)
    gl.drawElements(gl.TRIANGLES, 6, gl.UNSIGNED_SHORT, 0)
  }

  updateSize(size) {
    this.size = size
    this.patterns = Utils.makePatterns(
      this.gl,
      this.noiseOptions.nPat,
      this.size,
      this.noiseOptions.alpha,
      this.noiseOptions.proportion,
      this.colorOptions.background,
      this.colorOptions.particles,
      this.noiseOptions.scale,
    )
    //debugger
    this.createTextures(this.gl, this.size, this.size)
  }

  updatePatterns() {
    this.patternsNeedUpdate = true
  }

  getFieldOptions() {
    return this.fieldOptions
  }

  setFieldOptions(fieldOptions) {
    this.fieldOptions = fieldOptions
  }

  setColorOptions(colorOptions) {
    this.colorOptions = colorOptions
  }

  getNoiseOptions() {
    return this.noiseOptions
  }
}
