let fieldOptions = {}
let options = {}
let colorOptions = {}
let size = 0
let ctx = undefined

export function constructor(
  canvas,
  fieldOptionsValue,
  optionsValue,
  colorOptionsValue,
  sizeValue,
) {
  ctx = canvas.getContext('2d')
  fieldOptions = fieldOptionsValue
  options = optionsValue
  colorOptions = colorOptionsValue
  size = sizeValue
}

export function render(t) {
  draw(ctx, size, size, options, colorOptions, fieldOptions)
}

export function draw(ctx, width, height, options, colorOptions, fieldOptions) {
  ctx.fillStyle = getRBGString(colorOptions.background)
  ctx.fillRect(0, 0, width, height)
  ctx.strokeStyle = getRBGString(colorOptions.particles)
  ctx.fillStyle = getRBGString(colorOptions.particles)
  // let offset = options.lineLength / 2 + 4
  // //debugger
  // for (let i = 0; i < width; i++) {
  //   for (let j = 0; j < height; j++) {
  //     if (
  //       i % options.particleDistance == 0 &&
  //       j % options.particleDistance == 0 &&
  //       i < width - options.lineLength / 2 - 6 &&
  //       j < height - options.lineLength / 2 - 6
  //     ) {
  //       let angle = (fieldOptions.angle * Math.PI) / 180
  //       let startPoint = {
  //         x: i + offset + (options.lineLength * Math.cos(angle)) / 2,
  //         y: j + offset + (options.lineLength * Math.sin(angle)) / 2,
  //       }
  //       let endPoint = {
  //         x: i + offset - (options.lineLength * Math.cos(angle)) / 2,
  //         y: j + offset - (options.lineLength * Math.sin(angle)) / 2,
  //       }
  //       //endPoint = { x: 20, y: 20 }
  //       //debugger
  //       drawArrow(ctx, options.lineWidth, startPoint, endPoint)
  //     }
  //   }
  // }

  let offset = options.lineLength / 2
  let realWidth = width - offset * 2
  let realHeight = height - offset * 2

  let nParticlesW = realWidth / options.particleDistance + 1
  let nParticlesH = realHeight / options.particleDistance + 1

  for (let j = 0; j < nParticlesH / 2; j++) {
    for (let i = 0; i < nParticlesW / 2; i++) {
      let angle = fieldOptions.angle * Math.PI / 180
      let startPoint = {
        x:
          realWidth / 2 +
          offset +
          i * options.particleDistance +
          options.lineLength * Math.cos(angle) / 2,
        y:
          realHeight / 2 +
          offset -
          j * options.particleDistance +
          options.lineLength * Math.sin(angle) / 2,
      }
      let endPoint = {
        x:
          realWidth / 2 +
          i * options.particleDistance +
          offset -
          options.lineLength * Math.cos(angle) / 2,
        y:
          realHeight / 2 +
          offset -
          j * options.particleDistance -
          options.lineLength * Math.sin(angle) / 2,
      }
      drawArrow(ctx, options.lineWidth, startPoint, endPoint)

      let startPointbis = {
        x:
          realWidth / 2 +
          offset -
          options.particleDistance * i +
          options.lineLength * Math.cos(angle) / 2,
        y:
          realHeight / 2 +
          offset -
          j * options.particleDistance +
          options.lineLength * Math.sin(angle) / 2,
      }

      let endPointbis = {
        x:
          realWidth / 2 +
          offset -
          i * options.particleDistance -
          options.lineLength * Math.cos(angle) / 2,
        y:
          realHeight / 2 +
          offset -
          j * options.particleDistance -
          options.lineLength * Math.sin(angle) / 2,
      }

      //debugger
      drawArrow(ctx, options.lineWidth, startPointbis, endPointbis)

      let startPointdown = {
        x:
          realWidth / 2 +
          offset +
          i * options.particleDistance +
          options.lineLength * Math.cos(angle) / 2,
        y:
          realHeight / 2 +
          offset +
          j * options.particleDistance +
          options.lineLength * Math.sin(angle) / 2,
      }
      let endPointdown = {
        x:
          realWidth / 2 +
          i * options.particleDistance +
          offset -
          options.lineLength * Math.cos(angle) / 2,
        y:
          realHeight / 2 +
          offset +
          j * options.particleDistance -
          options.lineLength * Math.sin(angle) / 2,
      }
      drawArrow(ctx, options.lineWidth, startPointdown, endPointdown)

      let startPointbisdown = {
        x:
          realWidth / 2 +
          offset -
          options.particleDistance * i +
          options.lineLength * Math.cos(angle) / 2,
        y:
          realHeight / 2 +
          offset +
          j * options.particleDistance +
          options.lineLength * Math.sin(angle) / 2,
      }

      let endPointbisdown = {
        x:
          realWidth / 2 +
          offset -
          i * options.particleDistance -
          options.lineLength * Math.cos(angle) / 2,
        y:
          realHeight / 2 +
          offset +
          j * options.particleDistance -
          options.lineLength * Math.sin(angle) / 2,
      }

      //debugger
      drawArrow(ctx, options.lineWidth, startPointbisdown, endPointbisdown)
    }

    //debugger
  }
}

function getRBGString(rgb) {
  return 'rgb(' + rgb[0] + ',' + rgb[1] + ',' + rgb[2] + ')'
}

//http://jsfiddle.net/Sg7EZ/804/

function drawArrow(ctx, width, startPoint, endPoint) {
  //debugger
  ctx.lineWidth = width
  ctx.beginPath()
  ctx.moveTo(startPoint.x, startPoint.y)
  ctx.lineTo(endPoint.x, endPoint.y)
  ctx.stroke()

  // draw the starting arrowhead
  var startRadians = Math.atan(
    (endPoint.y - startPoint.y) / (endPoint.x - startPoint.x),
  )
  if (endPoint.x == startPoint.x) {
    if (startPoint.y < endPoint.y) {
      startRadians = -Math.PI / 2
    } else {
      startRadians = Math.PI / 2
    }
  }
  startRadians += (endPoint.x > startPoint.x ? -90 : 90) * Math.PI / 180
  drawArrowhead(ctx, startPoint.x, startPoint.y, startRadians, width)
  // // draw the ending arrowhead
  // var endRadians=Math.atan((endPoint.y-startPoint.y)/(endPoint.x-startPoint.x));
  // endRadians+=((endPoint.x>this.x1)?90:-90)*Math.PI/180;
  // this.drawArrowhead(ctx,this.x2,this.y2,endRadians);
}

function drawArrowhead(ctx, x, y, radians, lineWidth) {
  ctx.save()
  ctx.beginPath()
  ctx.translate(x, y)
  ctx.rotate(radians)
  ctx.moveTo(0, 0)
  ctx.lineTo(lineWidth * 2, lineWidth * 3)
  ctx.lineTo(-lineWidth * 2, lineWidth * 3)
  ctx.closePath()
  ctx.restore()
  ctx.fill()
}
