import * as glm from 'gl-matrix'

export class Camera {
  constructor(gl, width, height, pixelsPerUnit) {
    this.width = width
    this.height = height
    this.fieldOfView = (45 * Math.PI) / 180
    this.aspect = gl.canvas.clientWidth / gl.canvas.clientHeight
    this.zNear = -1
    this.zFar = 100.0
    this.projectionMatrix = glm.mat4.create()
    this.left = -width / (2 * pixelsPerUnit)
    this.right = width / (2 * pixelsPerUnit)
    this.bottom = -height / (2 * pixelsPerUnit)
    this.top = height / (2 * pixelsPerUnit)
    this.pixelsPerUnit = pixelsPerUnit
    //debugger

    // glm.mat4.perspective(
    //   this.projectionMatrix,
    //   this.fieldOfView,
    //   this.aspect,
    //   this.zNear,
    //   this.zFar,

    // )
    gl.viewport(0, 0, width, height)
    glm.mat4.ortho(
      this.projectionMatrix,
      this.left,
      this.right,
      this.bottom,
      this.top,
      this.zNear,
      this.zFar,
    )

    // Set the drawing position to the "identity" point, which is
    // the center of the scene.
    this.modelViewMatrix = glm.mat4.create()
    glm.mat4.identity(this.modelViewMatrix)
    glm.mat4.lookAt(
      this.modelViewMatrix,
      [0.5, 0.5, 8],
      [0.5, 0.5, 0],
      [0, 1, 0],
    )
    // Now move the drawing position a bit to where we want to
    // start drawing the square.

    // glm.mat4.translate(
    //   this.modelViewMatrix, // destination matrix
    //   this.modelViewMatrix, // matrix to translate
    //   [-0.0, 0.0, -6.0],
    // ) // amount to translate
  }

  update(gl, width, height, pixelsPerUnit) {
    this.width = width
    this.height = height
    this.projectionMatrix = glm.mat4.create()
    this.left = -width / (2 * pixelsPerUnit)
    this.right = width / (2 * pixelsPerUnit)
    this.bottom = -height / (2 * pixelsPerUnit)
    this.top = height / (2 * pixelsPerUnit)

    this.pixelsPerUnit = pixelsPerUnit
    gl.viewport(0, 0, width, height)
    glm.mat4.ortho(
      this.projectionMatrix,
      this.left,
      this.right,
      this.bottom,
      this.top,
      this.zNear,
      this.zFar,
    )
    // Set the drawing position to the "identity" point, which is
    // the center of the scene.
    this.modelViewMatrix = glm.mat4.create()
    glm.mat4.identity(this.modelViewMatrix)
    glm.mat4.lookAt(
      this.modelViewMatrix,
      [0.5, 0.5, 8],
      [0.5, 0.5, 0],
      [0, 1, 0],
    )
  }

  setCenter(centerCoords) {
    //debugger
    //this.modelViewMatrix = glm.mat4.create()
    this.center = centerCoords
    glm.mat4.identity(this.modelViewMatrix)
    glm.mat4.lookAt(
      this.modelViewMatrix,
      [centerCoords.x, centerCoords.y, 8],
      [centerCoords.x, centerCoords.y, 0],
      [0, 1, 0],
    )

    // this.projectionMatrix = glm.mat4.create()
    // this.left = -width / (2 * pixelsPerUnit)
    // this.right = width / (2 * pixelsPerUnit)
    // this.bottom = -height / (2 * pixelsPerUnit)
    // this.top = height / (2 * pixelsPerUnit)
    // this.pixelsPerUnit = pixelsPerUnit
    //gl.viewport(0, 0, width, height)
    // glm.mat4.ortho(
    //   this.projectionMatrix,
    //   -1000,
    //   1000,
    //   -1000,
    //   1000,
    //   this.zNear,
    //   this.zFar,
    // )
  }

  setBounds(leftCoord, rightCoord, bottomCoord, topCoord) {
    let xDiff = rightCoord - leftCoord
    let yDiff = topCoord - bottomCoord
    let maxDiff = Math.max(yDiff, xDiff)
    let left = (leftCoord - rightCoord) / 2
    let right = (rightCoord - leftCoord) / 2
    let top = (topCoord - bottomCoord) / 2
    let bottom = (bottomCoord - topCoord) / 2
    //debugger
    // glm.mat4.ortho(
    //   this.projectionMatrix,
    //   left,
    //   right,
    //   bottom,
    //   top,
    //   this.zNear,
    //   this.zFar,
    // )

    this.projectionMatrix = glm.mat4.create()
    this.left = -maxDiff / 2
    this.right = maxDiff / 2
    this.bottom = -maxDiff / 2
    this.top = maxDiff / 2
    glm.mat4.ortho(
      this.projectionMatrix,
      this.left,
      this.right,
      this.bottom,
      this.top,
      this.zNear,
      this.zFar,
    )
    this.pixelsPerUnit = maxDiff / (maxDiff == xDiff ? this.width : this.height)
  }
  getProjectionMatrix() {
    return this.projectionMatrix
  }

  getModelViewMatrix() {
    return this.modelViewMatrix
  }

  getScreenCoords(x, y) {
    return {
      // x: (x - this.left) / this.pixelsPerUnit,
      // y: (y - this.bottom) / this.pixelsPerUnit,
      x:
        ((x - (this.center.x + this.left)) * this.width) /
        (this.right - this.left),
      y:
        ((y - (this.center.y + this.bottom)) * this.height) /
        (this.top - this.bottom),
    }
  }

  resetViewport(gl) {
    gl.viewport(0, 0, this.width, this.height)
  }

  setViewport(gl, width, height) {
    gl.viewport(0, 0, width, height)
    this.width = width
    this.height = height
  }

  getCameraForRectangle(center, realWidth, realHeight) {
    let projectionMatrix = glm.mat4.create()
    let left = -realWidth / 2
    let right = realWidth / 2
    let top = realHeight / 2
    let bottom = -realHeight / 2
    glm.mat4.ortho(
      projectionMatrix,
      left,
      right,
      bottom,
      top,
      this.zNear,
      this.zFar,
    )
    let modelViewMatrix = glm.mat4.create()
    glm.mat4.identity(modelViewMatrix)
    glm.mat4.lookAt(modelViewMatrix, [0.5, 0.5, 8], [0.5, 0.5, 0], [0, 1, 0])
    return {
      projectionMatrix: projectionMatrix,
      modelViewMatrix: modelViewMatrix,
    }
  }

  getPixelsPerUnit() {
    return this.pixelsPerUnit
  }

  getWidth() {
    return this.width
  }

  getHeight() {
    return this.height
  }
}
