import { CanvasGL } from './CanvasGL'
import { AnimatedTextureCanvas } from './AnimatedTextureCanvas'
import { OlicCanvas } from './OlicCanvas'
import * as staticArrows from './staticArrow'
import resultsErrorAngle from './img/errorpertechnique.jpg'
import resultsErrorColor from './img/errorpertechniquecolor.jpg'
import resultsTimeAngle from './img/timepertechnique.jpg'
import resultsTimeColor from './img/timepertechniquecolor.jpg'

let canvasIBFV = document.getElementById('ibfv_canvas')
let canvasStatic = document.getElementById('static_canvas')
let canvasDT = document.getElementById('dt_canvas')
let canvasOLIC = document.getElementById('olic_canvas')
let startSize = 256

const pxRatio = Math.max(Math.floor(window.devicePixelRatio) || 1, 2)
//canvasIBFV.width = canvasIBFV.clientWidth
//canvasIBFV.height = canvasIBFV.clientHeight
//canvasDT.style.width = startSize + 'px'
//canvasDT.style.height = startSize + 'px'
//canvasDT.width = canvasIBFV.clientWidth * pxRatio
//canvasDT.height = canvasIBFV.clientHeight * pxRatio

document.getElementById('resultsAnglesImg').src = resultsErrorAngle
document.getElementById('resultsColorsImg').src = resultsErrorColor
document.getElementById('resultsAnglesImgTime').src = resultsTimeAngle
document.getElementById('resultsColorsImgTime').src = resultsTimeColor

let fieldOptions = { vx: 10, vy: 10, angle: -90 }
let frolicOptions = {
  lineLength: 32,
  steps: 50,
  nParticles: 50,
  randomness: 20,
  size: 2,
}
let animatedTextureOptions = {
  size: 256,
  alpha: 255,
  proportion: 0.5,
  speed: 64,
  scale: 1,
  pixelSize: 4,
}
let colorOptions = {
  //background: '#ffffff',
  background: [255, 255, 255],
  particles: [69, 55, 129],
}
let licOptions = {
  lineLength: 20,
  nParticles: 50,
  size: 1,
  randomness: 4,
  width: 256,
  height: 256,
  speed: 40,
  particleDistance: 10,
  scale: 1,
}

let staticOptions = {
  lineWidth: 2,
  particleDistance: 28,
  lineLength: 16,
}

const canvasGL = new CanvasGL(
  canvasIBFV,
  fieldOptions,
  undefined,
  colorOptions,
  startSize,
)

const animatedTextureCanvasGL = new AnimatedTextureCanvas(
  canvasDT,
  fieldOptions,
  animatedTextureOptions,
  colorOptions,
  startSize,
)

const olicCanvasGL = new OlicCanvas(
  canvasOLIC,
  fieldOptions,
  licOptions,
  colorOptions,
  startSize,
)

function init() {
    canvasGL.init()
    animatedTextureCanvasGL.init()
    olicCanvasGL.init()
    frame()
}

let t = 0
let startTime = -1
let currentTime = 0
let nFrames = 2
let lastTime = -1
let accTime = 0
function frame() {
  if (startTime == -1) {
    startTime = Date.now()
    lastTime = startTime
  }
currentTime = (Date.now() - startTime) / 1000
  animatedTextureCanvasGL.render(currentTime, colorOptions)
  lastTime = Date.now()

  canvasGL.render(currentTime)

  currentTime = (Date.now() - startTime) / 1000
  olicCanvasGL.render(currentTime)
  staticArrows.draw(
    canvasStatic.getContext('2d'),
    canvasStatic.width,
    canvasStatic.height,
    staticOptions,
    colorOptions,
    fieldOptions,
  )

  //animatedTextureThree.animate(t)

  t += 1

  //secondCanvasGL.render()
  requestAnimationFrame(frame)
}

init()