//https://github.com/mapbox/webgl-wind/blob/master/src/util.js

//
// Initialize a shader program, so WebGL knows how to draw our data
//
export function initShaderProgram(gl, vsSource, fsSource) {
  const vertexShader = loadShader(gl, gl.VERTEX_SHADER, vsSource)
  const fragmentShader = loadShader(gl, gl.FRAGMENT_SHADER, fsSource)

  // Create the shader program

  const shaderProgram = gl.createProgram()
  gl.attachShader(shaderProgram, vertexShader)
  gl.attachShader(shaderProgram, fragmentShader)
  gl.linkProgram(shaderProgram)

  // If creating the shader program failed, alert

  if (!gl.getProgramParameter(shaderProgram, gl.LINK_STATUS)) {
    alert(
      'Unable to initialize the shader program: ' +
        gl.getProgramInfoLog(shaderProgram),
    )
    return null
  }

  const wrapper = { program: shaderProgram }

  const numAttributes = gl.getProgramParameter(
    shaderProgram,
    gl.ACTIVE_ATTRIBUTES,
  )
  for (let i = 0; i < numAttributes; i++) {
    const attribute = gl.getActiveAttrib(shaderProgram, i)
    wrapper[attribute.name] = gl.getAttribLocation(
      shaderProgram,
      attribute.name,
    )
  }
  const numUniforms = gl.getProgramParameter(shaderProgram, gl.ACTIVE_UNIFORMS)
  for (let i = 0; i < numUniforms; i++) {
    const uniform = gl.getActiveUniform(shaderProgram, i)
    wrapper[uniform.name] = gl.getUniformLocation(shaderProgram, uniform.name)
  }

  return wrapper
}

//
// creates a shader of the given type, uploads the source and
// compiles it.
//
export function loadShader(gl, type, source) {
  const shader = gl.createShader(type)

  // Send the source to the shader object

  gl.shaderSource(shader, source)

  // Compile the shader program

  gl.compileShader(shader)

  // See if it compiled successfully

  if (!gl.getShaderParameter(shader, gl.COMPILE_STATUS)) {
    alert(
      'An error occurred compiling the shaders: ' + gl.getShaderInfoLog(shader),
    )
    gl.deleteShader(shader)
    return null
  }

  return shader
}

export function createBuffer(gl, data) {
  const buffer = gl.createBuffer()
  gl.bindBuffer(gl.ARRAY_BUFFER, buffer)
  gl.bufferData(gl.ARRAY_BUFFER, data, gl.STATIC_DRAW)
  return buffer
}

export function createIndexBuffer(gl, data) {
  const buffer = gl.createBuffer()
  gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, buffer)
  gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, data, gl.STATIC_DRAW)
  return buffer
}

export function bindAttribute(gl, buffer, attribute, numComponents) {
  gl.bindBuffer(gl.ARRAY_BUFFER, buffer)
  gl.vertexAttribPointer(attribute, numComponents, gl.FLOAT, false, 0, 0)
  gl.enableVertexAttribArray(attribute)
}

export function createTexture(gl, filter, data, width, height, internalFormat) {
  internalFormat = internalFormat == undefined ? gl.RGBA : internalFormat
  console.log('internalFormat ' + internalFormat)
  const texture = gl.createTexture()
  gl.bindTexture(gl.TEXTURE_2D, texture)
  gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE)
  gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE)
  gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, filter)
  gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, filter)
  if (data instanceof Uint8Array) {
    gl.texImage2D(
      gl.TEXTURE_2D,
      0,
      internalFormat,
      width,
      height,
      0,
      internalFormat,
      gl.UNSIGNED_BYTE,
      data,
    )
  } else {
    gl.texImage2D(
      gl.TEXTURE_2D,
      0,
      internalFormat,
      internalFormat,
      gl.UNSIGNED_BYTE,
      data,
    )
  }
  gl.bindTexture(gl.TEXTURE_2D, null)
  return texture
}

export function createRepeatedTexture(
  gl,
  filter,
  data,
  width,
  height,
  internalFormat,
) {
  internalFormat = internalFormat == undefined ? gl.RGBA : internalFormat
  console.log('internalFormat ' + internalFormat)
  const texture = gl.createTexture()
  gl.bindTexture(gl.TEXTURE_2D, texture)

  //gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_BASE_LEVEL, 0)
  //gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAX_LEVEL, 0)
  if (data instanceof Uint8Array) {
    gl.texImage2D(
      gl.TEXTURE_2D,
      0,
      internalFormat,
      width,
      height,
      0,
      internalFormat,
      gl.UNSIGNED_BYTE,
      data,
    )
  } else {
    gl.texImage2D(
      gl.TEXTURE_2D,
      0,
      internalFormat,
      internalFormat,
      gl.UNSIGNED_BYTE,
      data,
    )
  }
  //gl.generateMipmap(gl.TEXTURE_2D)
  gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.REPEAT)
  gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.REPEAT)
  gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, filter)
  gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, filter)
  gl.bindTexture(gl.TEXTURE_2D, null)
  return texture
}

export function bindTexture(gl, texture, unit) {
  gl.activeTexture(gl.TEXTURE0 + unit)
  gl.bindTexture(gl.TEXTURE_2D, texture)
}

export function makePatterns(
  gl,
  nPatterns,
  noiseSize,
  alpha,
  proportion,
  backgroundColor,
  particlesColor,
  scale,
) {
  let lut = []
  let phase = []
  let patterns = []
  noiseSize = noiseSize / scale
  let limit = Math.floor(proportion * 256)
  for (let i = 0; i < 256; i++) {
    lut[i] = i < limit ? 0 : 255
  }

  for (let i = 0; i < noiseSize; i++) {
    phase[i] = []
    for (let j = 0; j < noiseSize; j++) {
      phase[i][j] = Math.floor(Math.random() * 255)
    }
  }

  for (let k = 0; k < nPatterns; k++) {
    const texData = new Uint8Array(noiseSize * noiseSize * 4)
    let t = (k * 256) / nPatterns
    for (let i = 0; i < noiseSize; i++) {
      for (let j = 0; j < noiseSize; j++) {
        texData[(i * noiseSize + (j % noiseSize)) * 4] =
          lut[(t + phase[i][j]) % 256] == 0
            ? backgroundColor[0]
            : particlesColor[0]
        texData[(i * noiseSize + (j % noiseSize)) * 4 + 1] =
          lut[(t + phase[i][j]) % 256] == 0
            ? backgroundColor[1]
            : particlesColor[1]
        texData[(i * noiseSize + (j % noiseSize)) * 4 + 2] =
          lut[(t + phase[i][j]) % 256] == 0
            ? backgroundColor[2]
            : particlesColor[2]
        texData[(i * noiseSize + (j % noiseSize)) * 4 + 3] = alpha
        // texData[(i * noiseSize + (j % noiseSize)) * 4] = 255
        // texData[(i * noiseSize + (j % noiseSize)) * 4 + 1] = 0
        // texData[(i * noiseSize + (j % noiseSize)) * 4 + 2] = 0
        // texData[(i * noiseSize + (j % noiseSize)) * 4 + 3] = 255
      }
    }
    patterns.push(createTexture(gl, gl.LINEAR, texData, noiseSize, noiseSize))
  }
  console.log('finish making patterns')
  return patterns
}

