      //   value > 127 ? colorOptions.background[1] : colorOptions.particles[1]
      // texData[(i * noiseSize + (j % noiseSize)) * 4 + 3] = 100

      // texData[(i * noiseSize + (j % noiseSize)) * 4] = colorOptions.particles[0]
