      //   value > proportion * 255 ? 0 : alpha

      // texData[(i * noiseSize + j % noiseSize) * 4] =
      //   value > proportion * 255
      //     ? colorOptions.particles[0]
      //     : colorOptions.background[0]
      // texData[(i * noiseSize + j % noiseSize) * 4 + 1] =
      //   value > proportion * 255
      //     ? colorOptions.particles[1]
      //     : colorOptions.background[1]
      // texData[(i * noiseSize + j % noiseSize) * 4 + 2] =
