import { Camera } from './Camera'
import { HeatMap } from './heatmap'
import { ParticlesCanvas } from './ParticlesCanvas'

export class CanvasGL {
  constructor(canvas, fieldOptions, options, colorOptions, startSize) {
    this.pxRatio = Math.max(Math.floor(window.devicePixelRatio) || 1, 2)
    // canvas.width = canvas.clientWidth
    // canvas.height = canvas.clientHeight
    this.size = startSize
    canvas.style.width = this.size + 'px'
    canvas.style.height = this.size + 'px'
    //canvas.width = canvas.clientWidth * this.pxRatio
    //canvas.height = canvas.clientHeight * this.pxRatio
    this.gl = canvas.getContext('webgl', { antialiasing: false })
    this.camera = new Camera(
      this.gl,
      canvas.width,
      canvas.height,
      startSize,
    )
    //debugger
    this.canvas = canvas
    //this.gl.viewport(0, 0, 512, 512)
    //this.heatMap = new HeatMap(this.gl, 8)
    this.particlesCanvas = new ParticlesCanvas(
      this.gl,
      50,
      1,
      // this.canvas.width,
      // this.canvas.height,
      this.canvas.width,
      this.canvas.height,
      this.camera.getPixelsPerUnit(),
      startSize,
      fieldOptions,
      colorOptions,
      canvas.width,
    )
    this.render = this.render.bind(this)
    this.options = { size: startSize }
    this.accTime = 0
    this.lastTime = -1
  }

  init() {
    if (this.gl === null) {
      alert(
        'Unable to initialize WebGL. Your browser or machine may not support it.',
      )
      return
    }

    // Set clear color to black, fully opaque
    this.gl.clearColor(0.0, 0.0, 0.0, 1.0)
    // Clear the color buffer with specified clear color
    this.gl.clear(this.gl.COLOR_BUFFER_BIT)
  }

  render(currentTime) {
    //this.gl.clearColor(0.0, 0.0, 0.0, 1.0) // Clear to black, fully opaque
    //this.gl.clear(this.gl.COLOR_BUFFER_BIT)
    //this.gl.clearDepth(1.0) // Clear everything
    //this.gl.enable(this.gl.DEPTH_TEST) // Enable depth testing
    //this.gl.depthFunc(this.gl.LEQUAL) // Near things obscure far things
    // this.heatMap.draw(
    //   this.camera.getProjectionMatrix(),
    //   this.camera.getModelViewMatrix(),
    // )
    //debugger
    if (this.lastTime == -1) {
      this.lastTime = currentTime
    }
    if (
      this.accTime >
      1 / this.particlesCanvas.getNoiseOptions().framesPerSecond
    ) {
      this.particlesCanvas.draw(
        this.camera.getProjectionMatrix(),
        this.camera.getModelViewMatrix(),
        this.gl,
        this.camera,
        currentTime,
      )
      this.accTime = 0
    } else {
      this.accTime += Date.now() / 1000 - this.lastTime / 1000
    }
    this.lastTime = Date.now()
  }

  updateSize(size) {
    this.size = size
    this.canvas.style.width = this.size + 'px'
    this.canvas.style.height = this.size + 'px'
    this.canvas.width = this.canvas.clientWidth * this.pxRatio
    this.canvas.height = this.canvas.clientHeight * this.pxRatio
    this.camera.update(
      this.gl,
      this.canvas.width,
      this.canvas.height,
      this.size * 2,
    )
    // this.canvas.width = this.options.size
    // this.canvas.height = this.options.size
    this.particlesCanvas.updateSize(this.canvas.width)
    //this.gl.viewport(0, 0, this.canvas.width, this.canvas.height)
  }
  updateAngleAndColor(fieldOptions, colorOptions) {
    this.particlesCanvas.setFieldOptions(fieldOptions)
    this.particlesCanvas.setColorOptions(colorOptions)
    this.particlesCanvas.updatePatterns()
  }

  getParticlesCanvas() {
    return this.particlesCanvas
  }

  getOptions() {
    return this.options
  }
}
