let texture = undefined
let kernel = []
let maxValue = 0
let sourceDroplets = []
let sourceOffsets = null

function createSparseNoise(width, height, nParticles, randomness, size) {
  //debugger
  let nChannels = 3
  let horizontalDivision = Math.floor(width / Math.sqrt(nParticles))
  let verticalDivision = Math.floor(height / Math.sqrt(nParticles))
  let texture = []
  for (let i = 0; i < width; i++) {
    texture[i] = []
    for (let j = 0; j < height; j++) {
      texture[i][j] = 0
    }
  }
  //debugger

  for (let i = 0; i < width; i++) {
    for (let j = 0; j < height; j++) {
      let randomNumber = Math.random()
      randomNumber = randomNumber * 2 - 1
      if (
        i % horizontalDivision == 0 &&
        j % verticalDivision == 0 &&
        i >= 2 &&
        j >= 2
      ) {
        let newI = Math.min(
          width - size - 1,
          Math.round(i + randomNumber * randomness),
        )
        let newJ = Math.min(
          height - size - 1,
          Math.round(j + randomNumber * randomness),
        )
        for (let k = Math.floor(-size / 2); k < Math.floor(size / 2); k++) {
          for (let l = Math.floor(-size / 2); l < Math.floor(size / 2); l++) {
            texture[newI + k][newJ + l] = 1
          }
        }
      }
    }
  }
  //debugger
  return texture
}

export function createSparseNoiseFixedSeparation(
  width,
  height,
  horizontalDivision,
  verticalDivision,
  randomness,
  size,
) {
  //debugger
  let nChannels = 3
  let texture = []
  for (let i = 0; i < width; i++) {
    texture[i] = []
    for (let j = 0; j < height; j++) {
      texture[i][j] = 0
    }
  }
  //debugger

  // for (let i = 0; i < width; i++) {
  //   for (let j = 0; j < height; j++) {
  //     let randomNumber = Math.random()
  //     randomNumber = randomNumber * 2 - 1
  //     if (
  //       i % horizontalDivision == 0 &&
  //       j % verticalDivision == 0 &&
  //       i >= 2 &&
  //       j >= 2
  //     ) {
  //       let newI = Math.min(
  //         width - size - 1,
  //         Math.round(i + randomNumber * randomness),
  //       )
  //       let newJ = Math.min(
  //         height - size - 1,
  //         Math.round(j + randomNumber * randomness),
  //       )
  //       for (let k = Math.floor(-size / 2); k < Math.floor(size / 2); k++) {
  //         for (let l = Math.floor(-size / 2); l < Math.floor(size / 2); l++) {
  //           texture[newI + k][newJ + l] = 1
  //         }
  //       }
  //     }
  //   }
  // }

  let offset = horizontalDivision / 2
  let newWidth = width - offset * 2
  let newHeight = height - offset * 2
  let nParticlesW = (width - offset) / horizontalDivision + 1
  let nParticlesH = (height - offset) / verticalDivision + 1
  for (let i = 0; i < nParticlesW / 2; i++) {
    for (let j = 0; j < nParticlesH / 2; j++) {
      let randomNumber = Math.random()
      randomNumber = randomNumber * 2 - 1

      if (i != 0 && j != 0) {
        let fwdi = newWidth / 2 + offset + i * horizontalDivision
        let fwdj = newHeight / 2 + offset + j * verticalDivision

        let bwdi = newWidth / 2 + offset - i * horizontalDivision
        let bwdj = newHeight / 2 + offset - j * verticalDivision

        let is = [bwdi, fwdi]
        let js = [bwdj, fwdj]

        for (let k = 0; k < 2; k++) {
          for (let l = 0; l < 2; l++) {
            let randomNumberi = Math.random()
            randomNumberi = randomNumber * 2 - 1
            let randomNumberj = Math.random()
            randomNumberj = randomNumber * 2 - 1
            let newI = Math.min(
              width - size - 1,
              Math.round(is[k] + randomNumberi * randomness),
            )
            newI = Math.max(0, newI)
            let newJ = Math.min(
              height - size - 1,
              Math.round(js[l] + randomNumberj * randomness),
            )
            newJ = Math.max(0, newJ)
            texture[newI][newJ] = 1
          }
        }
      } else if (i == 0 && j == 0) {
        let i0 = newWidth / 2 + offset
        let j0 = newHeight / 2 + offset
        // let randomNumberi = Math.random()
        // randomNumberi = randomNumber * 2 - 1
        // let randomNumberj = Math.random()
        // randomNumberj = randomNumber * 2 - 1
        // let newI = Math.min(
        //   width - size - 1,
        //   Math.round(i0 + randomNumberi * randomness),
        // )
        // newI = Math.max(0, newI)
        // let newJ = Math.min(
        //   height - size - 1,
        //   Math.round(j0 + randomNumberj * randomness),
        // )
        // newJ = Math.max(0, newJ)
        texture[i0][j0] = 1
      } else if (j == 0) {
        let j0 = newHeight / 2 + offset
        let bwdi = newWidth / 2 + offset - i * horizontalDivision
        let fwdi = newWidth / 2 + offset + i * horizontalDivision
        let is = [bwdi, fwdi]
        for (let k = 0; k < 2; k++) {
          let randomNumberi = Math.random()
          randomNumberi = randomNumber * 2 - 1
          let randomNumberj = Math.random()
          randomNumberj = randomNumber * 2 - 1
          let newI = Math.min(
            width - size - 1,
            Math.round(is[k] + randomNumberi * randomness),
          )
          newI = Math.max(0, newI)
          let newJ = Math.min(
            height - size - 1,
            Math.round(j0 + randomNumberj * randomness),
          )
          newJ = Math.max(0, newJ)
          texture[newI][newJ] = 1
        }
      } else if (i == 0) {
        let i0 = newWidth / 2 + offset
        let bwdj = newHeight / 2 + offset - j * verticalDivision
        let fwdj = newHeight / 2 + offset + j * verticalDivision
        let js = [bwdj, fwdj]
        for (let l = 0; l < 2; l++) {
          let randomNumberi = Math.random()
          randomNumberi = randomNumber * 2 - 1
          let randomNumberj = Math.random()
          randomNumberj = randomNumber * 2 - 1
          let newI = Math.min(
            width - size - 1,
            Math.round(i0 + randomNumberi * randomness),
          )
          newI = Math.max(0, newI)
          let newJ = Math.min(
            height - size - 1,
            Math.round(js[l] + randomNumberj * randomness),
          )
          newJ = Math.max(0, newJ)
          texture[newI][newJ] = 1
        }
      }
    }
  }
  //debugger
  //console.log('texture done')
  return texture
}

export function precalculateLic(
  width,
  height,
  kernellen,
  fieldOptions,
  licParams,
  colorOptions,
  speed,
  texture,
) {
  //debugger
  let sourceOffsets = []
  let sourceDroplets = []
  let sumKernel = 0
  //if (texture == undefined) {
  // texture = createSparseNoise(
  //   width,
  //   height,
  //   licParams.nParticles,
  //   licParams.randomness,
  //   licParams.size,
  // )
  if (texture == undefined) {
    texture = createSparseNoiseFixedSeparation(
      width,
      height,
      licParams.particleDistance,
      licParams.particleDistance,
      licParams.randomness,
      1,
    )
    console.log('should not be here')
  }
  for (let i = 0; i < kernellen; i++) {
    kernel[i] = 2 * (kernellen - i - 1)
    sumKernel += kernel[i]
  }
  maxValue = kernel[0] / sumKernel
  console.log('maxValue', maxValue)
  //}
  let result = []
  let licResult = null
  for (let i = 0; i < width; i++) {
    result[i] = []
    sourceDroplets[i] = []
    for (let j = 0; j < height; j++) {
      licResult = calculateLic(
        i,
        j,
        kernellen,
        width,
        height,
        texture,
        kernel,
        fieldOptions,
        0,
        0,
        true,
        speed,
      )
      //debugger
      sourceDroplets[i][j] = licResult.sourceDroplet
      result[i][j] = licResult.value
    }
  }
  //console.log('sourceDroplets', sourceDroplets)
  sourceOffsets = new Map()
  for (let i = 0; i < width; i++) {
    for (let j = 0; j < height; j++) {
      if (sourceDroplets[i][j].length > 0) {
        if (sourceOffsets.get(sourceDroplets[i][j][0][0]) == null) {
          sourceOffsets.set(sourceDroplets[i][j][0][0], new Map())
        }
        sourceOffsets
          .get(sourceDroplets[i][j][0][0])
          .set(
            sourceDroplets[i][j][0][1],
            Math.floor(Math.random() * kernellen),
          )
      }
    }
  }
  return {
    sourceDroplets: sourceDroplets,
    sourceOffsets: sourceOffsets,
    texture: texture,
    maxValue: maxValue,
  }
}

export function resetSourceOffsets() {
  sourceOffsets = null
  texture = undefined
}
export function drawLicToImage(
  width,
  height,
  kernellen,
  t,
  fieldOptions,
  licParams,
  colorOptions,
  textureFactor,
  scale,
  sourceOffsets,
  sourceDroplets,
  texture,
  maxValue,
) {
  //debugger
  let newWidth = width / textureFactor
  let newHeight = height / textureFactor
  // if (sourceOffsets == null) {
  //   precalculateLic(
  //     newWidth,
  //     newHeight,
  //     kernellen,
  //     fieldOptions,
  //     licParams,
  //     colorOptions,
  //     licParams.speed,
  //   )
  // }
  let sumKernel = 0
  // if (texture == undefined) {
  //   texture = createSparseNoise(
  //     width,
  //     height,
  //     licParams.nParticles,
  //     licParams.randomness,
  //     licParams.size,
  //     colorOptions.background,
  //     colorOptions.particles,
  //   )
  //   for (let i = 0; i < kernellen; i++) {
  //     kernel[i] = 2 * (kernellen - i - 1)
  //     sumKernel += kernel[i]
  //   }
  //   maxValue = kernel[0] / sumKernel
  //   console.log('maxValue', maxValue)
  // }
  //debugger

  let min = Number.MAX_VALUE
  let max = -Number.MAX_VALUE
  let result = []
  let licResult = null
  for (let i = 0; i < newWidth; i++) {
    result[i] = []
    for (let j = 0; j < newHeight; j++) {
      if (sourceDroplets[i][j].length > 0) {
        licResult = calculateLic(
          i,
          j,
          kernellen,
          newWidth,
          newHeight,
          texture,
          kernel,
          fieldOptions,
          t,
          sourceDroplets[i][j].length > 0
            ? sourceOffsets
                .get(sourceDroplets[i][j][0][0])
                .get(sourceDroplets[i][j][0][1])
            : 0,
          false,
          licParams.speed,
        )
        result[i][j] =
          licResult.value / (maxValue * sourceDroplets[i][j].length)
      } else {
        result[i][j] = 0
      }
    }
  }

  //debugger
  let scaledResult = []
  let scaledWidth = width * scale / textureFactor
  let scaledHeight = height * scale / textureFactor
  for (let i = 0; i < newWidth; i++) {
    for (let k = 0; k < scale; k++) {
      scaledResult[i * scale + k] = []
    }
    for (let j = 0; j < newHeight; j++) {
      for (let l = 0; l < scale; l++) {
        for (let k = 0; k < scale; k++) {
          scaledResult[i * scale + l][j * scale + k] = result[i][j]
        }
      }
    }
  }

  //debugger
  const texData = new Uint8Array(newWidth * newHeight * 4 * scale * scale)

  for (let i = 0; i < scaledWidth; i++) {
    for (let j = 0; j < scaledHeight; j++) {
      let value = scaledResult[i][j]
      if (value == 0) {
        texData[(i * scaledWidth + j % scaledHeight) * 4] =
          colorOptions.background[0]
        texData[(i * scaledWidth + j % scaledHeight) * 4 + 1] =
          colorOptions.background[1]
        texData[(i * scaledWidth + j % scaledHeight) * 4 + 2] =
          colorOptions.background[2]
        texData[(i * scaledWidth + j % scaledHeight) * 4 + 3] = 255
      }
      //let value = result[i][j] * 1 / 0.15
      else {
        texData[(i * scaledWidth + j % scaledHeight) * 4] =
          colorOptions.particles[0] * value +
          colorOptions.background[0] * (1 - value)
        texData[(i * scaledWidth + j % scaledHeight) * 4 + 1] =
          colorOptions.particles[1] * value +
          colorOptions.background[1] * (1 - value)
        texData[(i * scaledWidth + j % scaledHeight) * 4 + 2] =
          colorOptions.particles[2] * value +
          colorOptions.background[2] * (1 - value)
        texData[(i * scaledWidth + j % scaledHeight) * 4 + 3] = 255
      }
    }
  }
  //debugger
  return texData
}

export function updateTexture(width, height, licParams) {
  texture = createSparseNoise(
    width,
    height,
    licParams.nParticles,
    licParams.randomness,
    licParams.size,
  )
}

function calculateLic(
  x,
  y,
  l,
  width,
  height,
  texture,
  kernel,
  fieldOptions,
  t,
  offset,
  calculateSourceDroplet,
  speed,
) {
  //console.log('offset', offset)
  let sumfwd = 0
  let sumbwd = 0
  let miniDelta = 1 / (fieldOptions.vx * 100)
  let p0 = { x: x, y: y }
  let v = { x: fieldOptions.vx, y: fieldOptions.vy }
  let angle = fieldOptions.angle * Math.PI / 180
  v = { x: Math.sin(angle) * speed, y: Math.cos(angle) * speed }
  //v = { x: 10, y: 1 }
  //v = { x: 30, y: 15 }
  //debugger
  let previousPoint = { x: p0.x + 0.5, y: p0.y + 0.5 }
  let currentPoint = previousPoint
  let currentS = 0
  let current = null
  let kernelSum = 0
  let sourceDroplet = []
  //offset = 0
  for (let i = 0; i < l / 2; i++) {
    let currentdS = calculatedS(v, previousPoint, width, height)
    if (
      currentPoint.x >= 0 &&
      currentPoint.x < width &&
      currentPoint.y >= 0 &&
      currentPoint.y < height
    ) {
      if (calculateSourceDroplet) {
        //console.log('currentPoint', currentPoint)
        if (
          texture[Math.floor(currentPoint.x)][Math.floor(currentPoint.y)] != 0
        ) {
          sourceDroplet.push([
            Math.floor(currentPoint.x),
            Math.floor(currentPoint.y),
          ])
        }
      }
      sumfwd +=
        texture[Math.floor(currentPoint.x)][Math.floor(currentPoint.y)] *
        kernel[(offset + i + l / 2 + t) % l]
    }
    kernelSum += kernel[(offset + i + l / 2 + t) % l]
    //* currentdS
    // /kernelSum += currentKernel
    //}

    currentPoint = {
      x: previousPoint.x + v.x * currentdS,
      y: previousPoint.y + v.y * currentdS,
    }
    previousPoint = currentPoint
    //currentS = currentS + currentdS
  }

  //backward
  previousPoint = p0
  currentPoint = previousPoint
  for (let i = l / 2; i < l; i++) {
    //console.log('i', i)
    let currentdS = calculatedS(
      { x: -v.x, y: -v.y },
      previousPoint,
      width,
      height,
    )

    if (
      currentPoint.x >= 0 &&
      currentPoint.x < width &&
      currentPoint.y >= 0 &&
      currentPoint.y < height
    ) {
      //debugger
      //console.log('(l - 1 - i + t) % l', (l - 1 - i + t) % l)
      if (calculateSourceDroplet) {
        if (
          texture[Math.floor(currentPoint.x)][Math.floor(currentPoint.y)] != 0
        ) {
          sourceDroplet.push([
            Math.floor(currentPoint.x),
            Math.floor(currentPoint.y),
          ])
        }
      }
      sumbwd +=
        texture[Math.floor(currentPoint.x)][Math.floor(currentPoint.y)] *
        kernel[(offset + l - 1 - i + t) % l]
    }
    kernelSum += kernel[(offset + l - 1 - i + t) % l]
    //* currentdS

    currentPoint = {
      x: previousPoint.x - v.x * currentdS,
      y: previousPoint.y - v.y * currentdS,
    }
    previousPoint = currentPoint
  }
  //debugger
  //console.log('t', t)
  return {
    value: [(sumbwd + sumfwd) / kernelSum],
    sourceDroplet: sourceDroplet,
  }
}

function calculatedS(v, p, width, height) {
  let left = Math.floor(p.x)
  let right = left + 1
  let bottom = Math.floor(p.y)
  let top = bottom + 1
  let fx = 0
  let fy = 0
  let f = 0
  if (v.x != 0) {
    if (v.x > 0) {
      fx = (right - p.x) / v.x
    } else {
      if (left == p.x) {
        left--
      }
      fx = (left - p.x) / v.x
    }
  } else {
    fx = Number.MAX_VALUE
  }
  if (v.y != 0) {
    if (v.y > 0) {
      fy = (top - p.y) / v.y
    } else {
      if (bottom == p.y) {
        bottom--
      }
      fy = (bottom - p.y) / v.y
    }
  } else {
    fy = Number.MAX_VALUE
  }
  //debugger
  if (Math.abs(fx) < Math.abs(fy)) {
    return fx
  } else {
    return fy
  }
}

function advance(vx, vy, x, y, fx, fy, w, h) {
  let tx = 0
  let ty = 0
  if (vx >= 0) {
    tx = (1 - fx) / vx
  } else {
    tx = -fx / vx
  }
  if (vy >= 0) {
    ty = (1 - fy) / vy
  } else {
    ty = -fy / vy
  }
  if (tx < ty) {
    if (vx > 0) {
      x += 1
      fy += vy * tx
      fx = 0
    } else {
      x -= 1
      fy += vy * tx
      fx = 1
    }
  } else {
    if (vy > 0) {
      y += 1
      fx += vx * ty
      fy = 0
    } else {
      y -= 1
      fx += vx * ty
      fy = 1
    }
  }
  if (x < 0) x = 0
  if (y < 0) y = 0
  if (x >= h) x = h - 1
  if (y >= w) y = w - 1
  return { x: x, y: y, fx: fx, fy: fy }
}
